import React from "react";
import styled, { keyframes } from "styled-components";

const ParticleMV = keyframes`
  to {
    top: 30%;
  }
`
const DFireworkWrapper = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 130;
`;

const ParticleBox = styled.div`
  position: absolute;
  top: ${props => props.top};
  left: ${props => props.left};
  width: 300px;
  height: 500px;
  outline: 1px solid white;
  z-index: 150;
`;

const Particle = styled.div`
  position: absolute;
  width: 10px;
  height: 10px;
  top: 100%;
  left: 50%;
  background: yellow;
  border-radius: 100%;
  z-index: 150;
  animation: ${ParticleMV} 2s infinite;
`;


const Firework = () => {
  return (
    <DFireworkWrapper >
      <ParticleBox top={'20%'} left={'30%'}>
      <Particle />

      </ParticleBox>
    </DFireworkWrapper>
  );
};

export default Firework;
